<template>
    <div class="vinylvideo" id="vinylvideo">
      <transition appear name="fadein" >
        <div class="controls" v-show="showControls">
        <div class="playarea">
          <img src="../assets/Fast Backward.svg" class="speedup"/>
          <img src="../assets/Play button.svg" class="play" id="play"/>
          <img src="../assets/Fast forward.svg" class="speedup"/>
        </div>
        <div class="bar">
          <div class="line"></div>
        </div>
        <div class="volume">
          <img src="../assets/volumeicon.svg" class="volumebars"/>
          <div class="line"></div>
        </div>
        </div>
      </transition>
     <transition appear name="fadein" >
        <video ref="video" autoplay class="video" id="video">
          <source :src="videoUrl" type="video/mp4">
        </video>
     </transition>
    </div>
  </template>
  
  <script>
  export default {
    name: 'VinylVideo',
    components: {
    },
    data() {
      return {
        showControls: false,
        timeout: null,
        videoUrl: require("../assets/VAYA_Vinyle_et_Credits.mp4")
      }
    },
    computed: [],
    methods: {
      mousemove() {
        clearTimeout(this.timeout);
        this.showControls = true;
        this.timeout = setTimeout(() => {
          this.showControls = false;
          clearTimeout(this.timeout);
        }, 3000);
      },
      clickEvents(e) {
        console.log(e.target.id);
        switch(e.target.id) {
          case "play": {
            document.getElementById("video").play();
            break;
          }
        }
      },
      ended() {
        this.$refs.video.removeEventListener("ended", this.ended);
        window.location.href = "/";
      }
    },
    mounted() {
      // document.getElementById("vinylvideo").addEventListener("click", this.clickEvents)
      // document.getElementById("vinylvideo").addEventListener("mousemove", this.mousemove)
      this.$refs.video.addEventListener("ended", this.ended);

    }
  }
  </script>
  
